.logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 24px 16px 0;
    float: left;
    color: white;
    line-height: 31px;
    text-align: center;
}

.paperInfo {
    font-size: 1em;
    line-height: 1.3em;
    display: block;
}

h1 {
    font-family: "Dosis", sans-serif;
}

.center {
    display: block;
    margin: 0 auto;
}

.left {
    text-align: left;
}

.right {
    text-align: right;
}

.leftList {
    direction: ltr;
}

.rightList {
    direction: rtl;
}

.justify {
    text-align: justify;
}

.indent {
    text-indent: 2em;
}

.smaller {
    font-size: 0.5em;
}

.small {
    font-size: 0.8em;
}

.large {
    font-size: 1.4em;
}

.margin-left {
    margin-left: 20px;
}

.margin-left-large {
    margin-left: 50px;
}

.margin-right {
    margin-right: 20px;
}

.margin-right-large {
    margin-right: 50px;
}

.margin-top {
    margin-top: 20px;
}

.margin-top-large {
    margin-top: 50px;
}

.margin-top-larger {
    margin-top: 100px;
}

.margin-bottom {
    margin-bottom: 20px;
}

.margin-bottom-large {
    margin-bottom: 50px;
}

.italic {
    font-style: italic
}

.timeLine .small {
    margin-bottom: 0.2em;
}

.timeLine li {
    padding-bottom: 10px;
}